import { useQuery } from  "@tanstack/react-query";
import styled from "styled-components";
import { motion, useViewportScroll } from "framer-motion";
import { getMovieDetail, IGetMovieDetail, getMovieCredit, IGetSearchCredit } from "../api";
import { useHistory } from "react-router-dom"
import { makeImagePath } from "../utils";
import Like from "./Likethhis";

const Overlay = styled(motion.div)`
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.5);
  opacity: 0;
`;

const Bigmovie = styled(motion.div)`
  position: absolute;  
  width: 50%; 
  height: '80vh'; 
  left:0;
  right: 0;
  margin: 0 auto;
  background-color: ${props => props.theme.black.darker};
  border-radius: 15px;
  overflow: hidden;
`

const BigCover = styled.div`
  width: 100%;
  height: 500px;
  background-size: cover;
  background-position: center cennter;
`;

const BigTitle = styled.h3`
  color: ${props => props.theme.white.lighter};
  text-align: left;
  font-size: 50px;
  position: relative;
  top: -60px;
  padding: 20px;
  font-weight: 400;
`;

const InfoBox = styled.div`
  display: grid;
  grid-template-columns: minmax(50%, 50%) auto;
  grid-gap: 10px;
  margin-top: -60px;
`;

const SmallBox = styled.div``;

const SmallInfo = styled.p`
  color: ${props => props.theme.white.lighter};
  text-align: left;
  padding: 10px 20px;
`;

const BigOverview = styled.p`
  padding: 10px 0 5vh 20px;
  color: ${props => props.theme.white.darker};
`;

const Dedetail = styled.span`
  color: ${props => props.theme.black.lighter};
`;

interface IProps{
  id: string;
}

function Detail({id}: IProps){
  const { data: detailData, isLoading: detailLoading } =
    useQuery<IGetMovieDetail>(["movie", `movie_detail`], () =>
      getMovieDetail(id)
    );
  const { data: creditData, isLoading: creditLoading } =
    useQuery<IGetSearchCredit>(["credit", `movie_credit`], () =>
      getMovieCredit(id)
    );
  console.log(detailData)

  const directing = creditData?.crew.find(
    (people) => people.known_for_department === "Directing"
  )

  const hour1 = detailData?.runtime as any / 60
  const hour = Math.floor(hour1)
  const min = detailData?.runtime as any% 60

  const history = useHistory();
  const onOverlayClick = () => history.goBack();
  const {scrollY} = useViewportScroll();



  return(
    <>
      <Overlay 
        onClick={onOverlayClick}
        animate={{opacity: 1}}
        exit={{opacity: 0}}
      />
      <Bigmovie
      style={{
        top: scrollY.get() +50
      }}>
      {detailData? 
        <>
        <BigCover
          style={{backgroundImage: `linear-gradient(to top, #181818, transparent),url(${makeImagePath(detailData.backdrop_path, "original")})`}}
        />
        <BigTitle>{detailData.title}
          <span style={{fontWeight: 200, fontSize: "20px"}}>({detailData.release_date.slice(0,4)})</span>
        </BigTitle>
      </>:null}
        <InfoBox>
        {detailData?
          <SmallBox>
            <SmallInfo>
              <Dedetail>Genres: </Dedetail>
              {detailData?.genres.slice(0,-1).map(genre => <span>{genre.name}, </span>)}
              {detailData?.genres.slice(-1).map(genre => <span>{genre.name}</span>)}
            </SmallInfo>
            <SmallInfo>
              <Dedetail>Runtime: </Dedetail> {hour} h {min}
            </SmallInfo>
            <BigOverview>{detailData.overview}</BigOverview>
          </SmallBox>
        :null}
        {creditData?
          <SmallBox>
            <SmallInfo>
              <Dedetail>Cast: </Dedetail>
              {creditData?.cast.slice(0,2).map(cast => <span>{cast.name}, </span>)}
              {creditData?.cast.slice(3,4).map(cast => <span>{cast.name}</span>)}
            </SmallInfo>
            {/* <SmallInfo key={directer}>{directer}</SmallInfo> */}
            <SmallInfo><Dedetail>Director: </Dedetail>{directing?.name}</SmallInfo>
          </SmallBox>
        :null}
        </InfoBox>
        <Like id={id}/>
    </Bigmovie>
    </>
  )
}

export default Detail;