const API_KEY = "2006802ebd86fd7e540d3704aa773df8";
const BASE = "https://api.themoviedb.org/3";

interface IMovie {
    id: number;
    backdrop_path: string;
    poster_path: string;
    title: string;
    overview: string;
    release_date: string;
}

interface ITv {
    id: number;
    backdrop_path: string;
    poster_path: string;
    name: string;
    overview: string;
    first_air_date: string;
}

interface IPeople {
    name: string;
    known_for_department: string;
}

interface IGenre {
    name: string;
}

export interface IGetMovieDetail {
    id: number;
    backdrop_path: string;
    title: string;
    original_language: string;
    release_date: string;
    genres: IGenre[];
    overview: string;
    runtime: number;
}

export interface IGetTvDetail {
    id: number;
    backdrop_path: string;
    name: string;
    original_language: string;
    first_air_date: string;
    genres: IGenre[];
    overview: string;
    episode_run_time: number;
}

export interface IGetMoviesResult {
    page: number;
    results: IMovie[];
    total_pages: number;
    total_results: number;
}

export interface IGetTvResult {
    page: number;
    results: ITv[];
    total_pages: number;
    total_results: number;
}

export interface IGetSearchResult {
    page: number;
    results: IMovie[];
    total_pages: number;
    total_results: number;
}

export interface IGetSearchResult2 {
    page: number;
    results: ITv[];
    total_pages: number;
    total_results: number;
}

export interface IGetSearchCredit {
    page: number;
    cast: IPeople[];
    crew: IPeople[];
}

export interface IProps{
    kind: string;
}


export function getMovies(){
    return fetch(`${BASE}/movie/now_playing?api_key=${API_KEY}`).then(
        (response) => response.json()
    );
}

export function getMovies3(){
    return fetch(`${BASE}/movie/top_rated?api_key=${API_KEY}`).then(
        (response) => response.json()
    );
}

export function getTv(){
    return fetch(`${BASE}/tv/on_the_air?api_key=${API_KEY}`).then(
        (response) => response.json()
    );
}

export function getTv3(){
    return fetch(`${BASE}/tv/top_rated?api_key=${API_KEY}`).then(
        (response) => response.json()
    );
}

export function searchMovies( keyword:string ){
    return fetch(`${BASE}/search/movie?api_key=${API_KEY}&query=${keyword}`).then(
        (response) => response.json()
    );
}

export function searchTvs( keyword:string ){
    return fetch(`${BASE}/search/tv?api_key=${API_KEY}&query=${keyword}`).then(
        (response) => response.json()
    );
}

export function getMovieDetail( id:string ){
    return fetch(`${BASE}/movie/${id}?api_key=${API_KEY}`).then(
        (response) => response.json()
    );
}

export function getTvDetail( id:string ){
    return fetch(`${BASE}/tv/${id}?api_key=${API_KEY}`).then(
        (response) => response.json()
    );
}

export function getMovieCredit( id:string ){
    return fetch(`${BASE}/movie/${id}/credits?api_key=${API_KEY}`).then(
        (response) => response.json()
    );
}

export function getTvCredit( id:string ){
    return fetch(`${BASE}/tv/${id}/credits?api_key=${API_KEY}`).then(
        (response) => response.json()
    );
}

export function getMovieLike( id:string ){
    return fetch(`${BASE}/movie/${id}/similar?api_key=${API_KEY}`).then(
        (response) => response.json()
    );
}

export function getTvLike( id:string ){
    return fetch(`${BASE}/tv/${id}/similar?api_key=${API_KEY}`).then(
        (response) => response.json()
    );
}